  &__item {
    list-style: none;
    display: flex;
    align-items: center; }
  &__item:not(:last-child) {
    margin-right: 40px;
    &::after {
      content: '';
      display: block;
      height: 26px;
      width: 2px;
      background-color: rgba($gray, 0.3);
      margin-left: 40px; } }
  &__link {
    text-decoration: none;
    width: 202px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 10px 10px 15px rgba($gray, 0.2);
    border-radius: 60px;
    position: relative;
    overflow: hidden;
    span {
      font-size: 16px;
      color: $gray;
      z-index: 2; }
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -100%;
      height: 100%;
      width: 100%;
      background: linear-gradient(90deg, $orangeLight, $orangeDark);
      transition: left 0.2s linear; }
    &:hover {
      cursor: pointer;
      span {
        color: $white; }
      &::after {
        left: 0;
        transition: left 0.2s linear; } } } }
.burger, .drop-block {
  display: none; }
.calendar {
  &-widget {
    width: 294px;
    height: 174px;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    background-color: rgba($gray, 0.2);
